*, :before, :after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  text-rendering: optimizelegibility;
  font-family: Rubik, sans-serif;
  font-size: 62.5%;
  font-weight: 400;
}

body {
  color: #333;
  background-color: #d7d7d9;
  width: 100%;
  height: 100%;
  padding: 2rem;
