  border-radius: .5rem;
  align-items: center;
  gap: .3rem;
  padding: .4rem .6rem;
  font-family: inherit;
  font-weight: 500;
  transition: all .3s;
  display: flex;
}

.btn--menu {
  display: none;
}

.nav__icon {
  font-size: 2rem;
  transition: all .3s;
}

.nav__btn:hover {
  transform: translateY(-.3rem);
}

.search__results {
  flex-direction: column;
