  background-color: #efefef;
  border-left: .6rem solid #27ae60;
  box-shadow: 0 0 #0000004d;
}

.preview:hover {
  transform: translateX(.5rem);
}

.preview.preview--active:hover {
  transform: translateX(0);
}

.preview__link {
  color: #333;
  align-items: center;
  gap: 1rem;
  text-decoration: none;
  display: flex;
}

.preview--active .preview__link {
  cursor: default;
  -webkit-user-select: none;
  user-select: none;
}

.preview__fig {
  border-radius: 50%;
