  background: #27ae60;
  border-radius: 1rem;
  flex-direction: column;
  align-items: center;
  width: 8rem;
  height: 8rem;
  font-size: 1.3rem;
  display: flex;
}

.category__icon {
  display: none;
}

.spinner {
  border: 8px solid #efefef;
  border-top: 10px solid #27ae60;
  border-radius: 50%;
  width: 8rem;
  height: 8rem;
  margin: 0 auto;
  animation: 1s cubic-bezier(1, -.01, .37, 1.02) infinite spin;
  display: inline;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

